export class TextUtility {
    constructor() {
        if(this.instance)
            return this.instance;
        
        this.instance = this;
    }

    stripHtmlTags(body) {
        if(!this.temp)
            this.temp = document.createElement("div");
        this.temp.innerHTML = body;
        return this.temp.textContent || this.temp.innerText;
    }   
}