export class Note
{
    id = 0
    text = ""
    date = ""
    updateDate = ""
    openDate = ""
    generatedDate = ""
    type = ""
    patient = ""

    constructor()
    {
        this.date = Date.now()
        this.updateDate = Date.now()
        this.generatedDate = null;
    }

    setText(text)
    {
        this.text = text
    }

    setId(id)
    {
        this.id = id
    }

    getType()
    {
        return this.type
    }

    setType(type)
    {
        this.type = type
    }

    setPatient(patient){
        this.patient = patient
    }

    getPatient(){
        return this.patient
    }

    setOpenDate(date)
    {
        this.openDate = date
    }

    setGeneratedDate(date)
    {
        this.generatedDate = date
    }
}


export class Patient
{
    name = ""
    id = 0


    setPatientName(name) {
        if(!name || name === '')
            throw "PatientNameEmpty"
        this.name = name
    }

    getPatientName()
    {
        return this.name
    }

    setPatientId(id){
        this.id = id
    }

    getPatientId()
    {
        return this.id
    }
}