import React from 'react';
import {Platform, StyleSheet,ImageBackground, Text, View, Button, TextInput, Dimensions,KeyboardAvoidingView, StatusBar, ScrollView, FlatList, Keyboard, Alert } from 'react-native';
import { ListItem, Icon, Card, Overlay, ButtonGroup, Button as StyledButton } from 'react-native-elements'
import styles from './Styles'
import {PatientService} from './DomainService'
import {Alerts, WebAlert} from './Alerts'
import { AsyncStorage } from "react-native"
import {TextUtility} from './Utilities'
import { TokenComponent } from './TokenComponent'
import { BackupService } from './BackupService'

const images= {
    background: {img: require("./Assets/black-1072366_1920.jpg"), img2: require("./Assets/ontario.jpg")}
  }
  

export class HomeScreen extends React.Component {
  
    constructor(props){
      super(props)
      this.state = {
        notes: [],
        noteCategoriesVisible: false
      }
      new TokenComponent().createOrGetToken();
      new BackupService().doBackup();
      this.cacheBuster();

      setTimeout(() => this.refreshLastOpened(),100)
    }

    cacheBuster() {
      fetch('/meta.json')
      .then((response) => response.json())
      .then((meta) => {
        if(meta && meta.build_number != document.versionNumber) {
          if (caches) {
            // Service worker cache should be cleared with caches.delete()
            caches.keys().then(function(names) {
              for (let name of names) caches.delete(name);
            });
          }
          window.location.reload(true);
        }
      })
    }

    refreshLastOpened = () =>
    {
      let patientService = new PatientService()
      this.setState({notes : []}, () => {
        patientService.getLastOpenedNotes().then((innerNotes) => {       
          this.setState({notes: []})
  
          let promises = []
  
          innerNotes.forEach(innerNote => {
            promises.push(patientService.getPatientById(innerNote.patient))
          })
  
          Promise.all(promises).then(val => {
            this.valKeys = {}
            console.log(val)
            val.forEach(item => {
              if(item[0] !== undefined)
                this.valKeys[item[0].id] = item[0].name
            }, this)
  
            let notes = innerNotes.map(innerNote => {
              innerNote["patientName"] = this.valKeys[innerNote.patient]
              return innerNote
            })
  
            this.setState({notes: notes})
          })
  
  
        })
      })
    }
    
    componentDidMount() {
      this.listener = this.props.navigation.addListener("didFocus", this.refreshLastOpened);
      this.refreshLastOpened()
  
    }
  
    setNoteCategoriesVisible(state) {
      this.setState({noteCategoriesVisible: state});
    }
  
    reset() {
      //AsyncStorage.getAllKeys().then(AsyncStorage.multiRemove)
      AsyncStorage.setItem('notes', JSON.stringify(null), () => {
        this.refreshLastOpened()
      });
  
      AsyncStorage.setItem('patients', JSON.stringify(null), () => {
        this.refreshLastOpened()
      });
    }
    
    render() {
      return (
        <View style={styles.mainContainer}>
          <ImageBackground source={images['background'].img} style={styles.image}>
          <StatusBar barStyle="light-content" />
          <KeyboardAvoidingView style={{ flex: 1, alignItems: 'center', justifyContent: 'center'}}>
            <View style={{height: 1, width: 1, marginBottom: 10}}>
            </View>
            <View>
              {    
                Platform.OS !== 'web'?        
              <StyledButton
                icon={{name:'note-add', color:'#ffffff'}}
                backgroundColor='#03A9F4'
                buttonStyle={{borderRadius: 10, marginLeft: 0, marginRight: 0, marginBottom: 10, width: 400}}
                onPress={() => new Alerts().noteAlert(this.props.navigation)}
                title="Create a New Note"
                />
                :
              <StyledButton
                icon={{name:'note-add', color:'#ffffff'}}
                backgroundColor='#03A9F4'
                buttonStyle={{borderRadius: 10, marginLeft: 0, marginRight: 0, marginBottom: 10, width: 400}}
                onPress={() => this.setNoteCategoriesVisible(true)}
                title="Create a New Note"
                />
                
              }
              <WebAlert navigation={this.props.navigation} visible={this.state.noteCategoriesVisible} callback={(state) => this.setNoteCategoriesVisible(state)}/>
  
              <StyledButton
                icon={{name:'account-circle', color:'#ffffff'}}
                backgroundColor='#03A9F4'
                buttonStyle={{borderRadius: 10, marginLeft: 0, marginRight: 0, marginBottom: 10}}
                onPress={() => this.props.navigation.navigate('Patients')}
                title="View and add Patients"
              />
              <StyledButton
                icon={{name:'format-quote', color:'#ffffff'}}
                backgroundColor='#03A9F4'
                buttonStyle={{borderRadius: 10, marginLeft: 0, marginRight: 0, marginBottom: 0}}
                onPress={() => this.props.navigation.navigate('Categories')}
                title="Categories and Phrases"
              />

  
              <View style={{flexDirection: 'column', justifyContent:'center', borderRadius: 10, alignItems: "center"}}>              
  
                <Text style={{color: '#ffffff', marginTop: 20, justifyContent:'center'}}>Last Opened Notes</Text>
                {
                  //
                  this.state.notes.sort((a, b) => b.updateDate - a.updateDate).slice(0,7).filter(item => {
                      return item.text !== undefined
                  }).map((item) => {
                      let txt = new TextUtility().stripHtmlTags(item.text).replace(/(\r\n|\n|\r)/gm," ").slice(0, 30)
                      txt += new TextUtility().stripHtmlTags(item.text).length > 30? '...' : '';
                    return(
                      Platform.OS === 'ios'? <Button title={txt} key={item.id} onPress={() => {this.props.navigation.navigate('NoteDetail', {"action": "edit", "type": item.type, "patient": item.patient, "noteId": item.id, "text": item.text, "patientName": item.patientName})}}/> :
                      Platform.OS === 'web'? <StyledButton buttonStyle={styles.webButton} title={txt} key={item.id} onPress={() => {this.props.navigation.navigate('NoteDetail', {"action": "edit", "type": item.type, "patient": item.patient, "noteId": item.id, "text": item.text, "patientName": item.patientName})}}/> :null  
                    )
                  })
                }
                
                <View>{
                  //<Button title="Reset Notes" key={"reset"} onPress={() => this.reset()}/>
                }
                </View>
          </View>
  
            </View>
  
          </KeyboardAvoidingView>
          <View style={styles.footerContainer}>
            <View style={{flex:0, flexDirection: 'row'}}>
            <Icon
              name='cog'
              type='font-awesome'
              color='#aaa'
              onPress={() => this.props.navigation.navigate('Settings')}
            />
            <Text style={{color:'white', paddingLeft: 10}}>Version: {window.versionNumber}</Text>
            </View>
          </View>
          </ImageBackground>
        </View>
      );
    }  
  }