import {PatientRepository, MenuRepository, NoteRepository, SettingsRepository, TagRepository } from "./Repositories"
import { AsyncStorage } from "react-native"
import { Note } from "./Domain";

export class PatientService {
    saveNotes(notes)
    {
        return new NoteRepository().saveNotes(notes)
    }

    getNote(noteId)
    {
        return new NoteRepository().getNoteById(noteId)
    }

    getLastOpenedNotes(count = 10)
    {
        return new NoteRepository().getLastOpenedNotes(count)
    }

    getNotesByPhrase(phrase){
        return new NoteRepository().getNotesByPhrase(phrase)
    }

    setNoteOpenDate(note)
    {
        note = Object.assign(new Note(), note[0])
        note.setOpenDate(Date.now())
        new NoteRepository().updateNote(note)
    }

    getNoteCount()
    {
        return new NoteRepository().getNoteCount()
    }

    getAllPatients() {
        return new PatientRepository().findAll();
    }
    
    getPatientById(id)
    {
        if(id == -1) return null;
        return new PatientRepository().getPatientById(id)
    }

    getPatientNotesById(id)
    {
        if(id == -1) return null;
        return new PatientRepository().getPatientById(id).then((result, error) => {
            let patient = result
            if(result){
                return new NoteRepository().getPatientNotes(patient[0])
            }
            else{
                return new Promise((error, result) => {return []})
            }
            
        })
    }

    getPatientNotesByPatientName(patientName) {
        let patientRepository = new PatientRepository()

        return patientRepository.getPatientByName(patientName).then((result, error) => {
            return new NoteRepository().getPatientNotes(result[0])
        });
    }

    getPatientNotes(patient)
    {
        return new NoteRepository().getPatientNotes(patient)
    }

    deletePatientNote(noteId){
        return new NoteRepository().deleteNoteById(noteId)
    }

    savePatient(patient) {
        new PatientRepository().updatePatient(patient)
    }

    savePatientNote(patient, note)
    {
        if(patient.name === "")
            return

        let noteRepository = new NoteRepository()
        let patientRepository = new PatientRepository()

        patientRepository.getPatientByName(patient.name).then((result, error) => {
            console.log("find patient by name ")
            console.log(result)
            
            if(!result || !result.length){
                patientRepository.createPatient(patient).then((result) => {
                    let p = result
                    note.setPatient(p.id)
                    noteRepository.createNote(note)
                })               
            }
            else
            {
                console.log("found patient!")
                let p = result
                note.setPatient(p[0].id)
                noteRepository.createNote(note)
            }
            
        })
    
    }
    
}

export class TagService {
    createTag(tag) {
        return new TagRepository().createTag(tag)
    }

    deleteTag(tag) {
        return new TagRepository().deleteTag(tag)
    }

    getTagsByPatientAndType(tag) {
        return new TagRepository().getTagsByTypeAndEntity(tag)
    }

    getTagsByIdAndType(tag) {
        return new TagRepository().getTagsByIdAndType(tag)
    }

    getAllPatientTags() {
        return new TagRepository().getsTagsByType("patient");
    }
}

export class SettingsService {
    setSettings(settings){
        new SettingsRepository().saveSettings(settings);
    }

    getSettings() {
        return new SettingsRepository().getSettings();
    }

    clearSignatures() {
        let p = new PatientService()
        return AsyncStorage.getItem("notes").then((result) =>{
            let data = JSON.parse(result)
            let newData = []
            data.forEach(item => {                 
                let note = item.text.replace(/<img.+?>/gm,'<img class="signature" width=160 height=40 alt="signature" src="{{signature}}" />')
                item.text = note;
                newData.push(item);
            })
            p.saveNotes(newData).then(() => {
                window.location.reload(false);   
            })
        })
    }
}

export class MenuService {
    getMenu() {
        return new MenuRepository().getMenu()
    }

    saveMenu(menu) {
        new MenuRepository().saveMenu(menu)
    }

    searchPhrases(phrase) {
        return new MenuRepository().getPhraseByPhrase(phrase)
    }

    searchPhrasesByShortcut(shortcut) {
        return new MenuRepository().getPhraseByShortcut(shortcut)
    }
}