import jsPDF from 'jspdf'

class Template {
    generateFromDict(dictionary)
    {
        let template = this.getTemplate()
        Object.keys(dictionary).forEach(function(key) {
            let pattern = new RegExp('{{' + key + '}}', 'g')
            template = template.replace(pattern, dictionary[key])
        });

        return template
    }

    getTemplate() {
        return ""
    }
}


export class TextTemplate extends Template {
    getTemplate() {
        return `
        <style>
        p {
            font-size: 17px;
            /* font-family: Helvetica, Arial, sans-serif */
          }
    
          .ql-size-small {
            font-size: 14px
          }
    
          .ql-size-large {
            font-size: 20px
          }
    
          .ql-size-huge {
            font-size: 22px
          }

          .ql-align-center {
            text-align: center;
          }

          p {
            margin: -2px;
            line-height: normal;
          }
        </style>
        <div style="padding-top: 5px; padding-bottom: 5px; padding-left: 20px; padding-right: 20px;">
            {{note}}
        </div>
        `
    }
}

