import {SettingsService} from './DomainService'
import { AsyncStorage } from "react-native"

export class BackupService {
    constructor(backupType="api"){
      this.backupType = backupType;
    }
    collect(uid) {
        let importData = async (uid) => {
            try {
              const keys = await AsyncStorage.getAllKeys();
              const result = await AsyncStorage.multiGet(keys);
            
              let data = result;

              this.performBackup(uid, data)
            } catch (error) {
              console.error(error)
            }
        }
        importData(uid)
    }

    restoreFromBackup(data) {
      data = JSON.parse(data)
      data.forEach(item => {
        AsyncStorage.setItem(item[0], item[1]);
      })

    }

    performBackup(uid, data) {
      if(this.backupType === "file")
        this.sendFile(uid, JSON.stringify(data)) 
      else
        this.sendData(uid, JSON.stringify(data)) 
    }

    doBackup() {
        let settingsService = new SettingsService()
        settingsService.getSettings().then((settings) => {
          if(this.backupType === "file") {
            this.collect(settings.key)
          }
          else if(settings.lastBackupDate === undefined) {
            settings.lastBackupDate = Date.now()
            settingsService.setSettings(settings)
            //do a backup
          }
          else if(settings.lastBackupDate - Date.now()  < -(1000 * 60 * 60 * 24*2))
          {
            settings.lastBackupDate = Date.now()
            settingsService.setSettings(settings)
            this.collect(settings.key)        
          }
        });
      }

      sendFile(uid, content) {
        var a = document.createElement("a");
        var file = new Blob([content], {type: 'text/plain'});
        a.href = URL.createObjectURL(file);
        a.download = "backup.json";
        a.click();
      }
    
      sendData(uid, content) {
        const requestOptions = {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({ uid: uid, content: content })
        };
        let api = "http://localhost:3000/backups/"
        if(window.api !== undefined && window.api !== "{API}")
            api = window.api + "backups"

        fetch(api, requestOptions)
            .then(response => response.json())
            .then(data => console.log(data));
      }
}