import React from 'react';
import {Platform, StyleSheet,ImageBackground, Text, View, Button, TextInput, Dimensions,KeyboardAvoidingView, StatusBar, ScrollView, FlatList, Keyboard, Alert } from 'react-native';
import { ListItem, Icon, Card, Overlay, ButtonGroup, Button as StyledButton } from 'react-native-elements'
import styles from './Styles'
  
export  class WebAlert extends React.Component  {
    constructor(props){
      super(props)
      this.state = {
        'data': [],
      }
    }

    render()
    {
      return (
        this.props.visible === true? 
        <View>
          <StyledButton buttonStyle={styles.webButton2} title="Consult" onPress={() => this.props.navigation.navigate('NoteDetail', {action: "new", type: "consult", patientName: this.props.patientName})}></StyledButton>
          <StyledButton buttonStyle={styles.webButton2} title="Follow Up" onPress={() => this.props.navigation.navigate('NoteDetail', {action: "new", type: "followup", patientName: this.props.patientName})}></StyledButton>
          <StyledButton buttonStyle={styles.webButton2} title="Other" onPress={() => this.props.navigation.navigate('NoteDetail', {action: "new", type: "other", patientName:this.props.patientName})}></StyledButton>
          <StyledButton buttonStyle={styles.webButton2} title="Cancel" onPress={() => this.props.callback(false)}></StyledButton>
        </View>
        :
        <View></View>
      )
    }
  }


export class Alerts extends React.Component  {
    noteAlert(navigation, patientName){
        Alert.alert(
          'Type of Note',
          'Please select the type of note you would like to create',
          [
            {text: 'Consult', onPress: () => navigation.navigate('NoteDetail', {action: "new", type: "consult", patientName: patientName})},
            {text: 'Follow Up', onPress: () => navigation.navigate('NoteDetail',  {action: "new", type: "followup", patientName: patientName})},
            {text: 'Other', onPress: () => navigation.navigate('NoteDetail', {action: "new", type: "other", patientName: patientName})},
            {text: 'Cancel', onPress: () => console.log('cancel')},
          ],
          { cancelable: true }
        )
    }
  }