import React from 'react';
import { StyleSheet, Text, View, Button, TextInput, Dimensions,KeyboardAvoidingView, StatusBar, ScrollView, FlatList } from 'react-native';
import { createDrawerNavigator, createStackNavigator } from 'react-navigation';
import { Constants } from 'expo';
import {SideMenu, MenuItem} from './SideMenu'
import {SideMenuManageScreen} from './SideMenuManage'
import { HomeScreen } from './HomeScreen';
import { SettingsScreen } from './SettingsScreen';
import {PatientsScreen, PatientDetailScreen, NoteDetailScreen} from './Screens'
import {CombineNotesScreen} from './CombineNotesScreen'
import styles from './Styles'
import { AsyncStorage } from "react-native"

class NoteDrawerNavigator extends React.Component {
  render() {
    return <NotesSuggestionDrawer/>;
  }
}

const NotesSuggestionDrawer = createDrawerNavigator({
  Home: {
    screen: NoteDetailScreen,
    navigationOptions: {
      drawerLabel: () => null
    }
  },
},
{
  contentComponent: ({navigation}) => (<SideMenu navigation={navigation}/>),
  drawerWidth: 300
});


const MainStack = 
  createStackNavigator({
    Home: {
      screen: HomeScreen,
      navigationOptions: {
        title: 'Dashboard',
      },
    },
    NoteDetail: {
      screen: NotesSuggestionDrawer,
      navigationOptions: NoteDetailScreen.navigationOptions
    },
    Patients: {
      screen: PatientsScreen,
      navigationOptions: {
        title: 'Patient Listing',
      },
    },
    PatientDetail: {
      screen: PatientDetailScreen,
      navigationOptions: {
        title: 'Patient Detail',
      },
    },
    Categories: {
      screen: SideMenuManageScreen,
      navigationOptions: {
        title: 'Categories and Phrases',
      },
    },
    Settings: {
      screen: SettingsScreen,
      navigationOptions: {
        title: 'Settings',
      },
    },
    CombineNotesDetail: {
      screen: CombineNotesScreen,
      navigationOptions: {
        title: 'Combine Notes',
      },
    },
  },
  {
    initialRouteName: 'Home',
    gesturesEnabled: false,
  }
  ) 


export default class App extends React.Component {
  constructor(props){
    super(props)
    //AsyncStorage.getAllKeys().then(AsyncStorage.multiRemove)
  }

  render() {
    return <MainStack />;
    
  }
}