import React from 'react';
import {Platform, StyleSheet,ImageBackground, Text, View, Button, TextInput, Dimensions,KeyboardAvoidingView, StatusBar, ScrollView, FlatList, Keyboard, Alert } from 'react-native';
import {PatientService, MenuService, SettingsService, TagService} from './DomainService'
import { CheckBox, ListItem, Icon, Badge, Divider, Button as StyledButton, SearchBar } from 'react-native-elements'
import {TextUtility} from './Utilities'
import {PdfGenerator} from './PdfGenerator'


export class CombineNotesScreen extends React.Component {
    constructor(props){
      super(props)

      //var patientText = props.navigation.getParam("patientText", "")

      this.state = {
//        patientText: patientText,
        latestNotes: []
      }

      this.patients = {}
      console.log("drop")
//      new PatientService().getPatientNotesByPatientName(patientText).then((r,e) =>{
        new PatientService().getLastOpenedNotes(100).then((r,e) =>{
          let notes = r.map((note) => {
            note.checked = false;
            return note;
        })
        this.setState({latestNotes: notes.reverse()})
      });

      this.settingsService = new SettingsService()
      this.settingsService.getSettings().then((settings) => {
        this.setState({settings: settings})
      });

      new PatientService().getAllPatients().then((patients) => {
        patients = JSON.parse(patients)
        patients.forEach(patient => {
          this.patients[patient.id] = patient
        })
      });
    }

    selectDocument(id) {
      let notes = this.state.latestNotes;
      notes = notes.map((note) => {
        if(note.id == id) {
          note.checked = !note.checked;
        }
        return note
      });
      this.setState({latestNotes: notes})
    }


    generateDocument(type="default") {
      let patientService = new PatientService();

      let patientNotes = this.state.latestNotes.filter((note) => {
        return note.checked === true
      }).map(note => {
        return {note: note, patient: {id: note.patient}}
      });

      patientService.getAllPatients().then((patients) => {
        patients = JSON.parse(patients)
        let patientNotes2 = patientNotes.map((patientNote) => {
          patientNote.patient.name = patients.filter((patient) => patient.id === patientNote.patient.id)[0].name
          return patientNote
        })

        let pdfGenerator = new PdfGenerator(patientNotes2, this.state.settings.signature, this.state.settings.name, type);
        pdfGenerator.combinePdf();
      })
    }

    formatDate(date) {
      return date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + date.getDate() + " " + date.getHours() + ":" + date.getMinutes()

    }

    render() {
        return <View style={{flex:1}}>
          <View style={{flexDirection: "row"}}>
            <Button title="Generate Package" onPress={() => this.generateDocument()}></Button>
            <Button title="Generate Notes" onPress={() => this.generateDocument("note")}></Button>
            <Button title="Generate OrderSheet" onPress={() => this.generateDocument("order")}></Button>
          </View>
          <ScrollView style={{flex:1}}>
          <Text>Showing last 100 notes..</Text>
          {
            this.state.latestNotes.map((note) => {
              return <CheckBox
              title= { 
                  <View>
                    <Text>{this.patients[note.patient] && this.patients[note.patient].name}</Text>
                    <Text>Last Opened: {note.openDate && this.formatDate(new Date(note.openDate))}</Text>
                    <Text>Last Edited: {note.updateDate && this.formatDate(new Date(note.updateDate))}</Text>
                    <Text>{new TextUtility().stripHtmlTags(note.text).replace(/(\r\n|\n|\r)/gm," ").slice(0, 100)}</Text>
                  </View>
              }
              iconRight
              iconType='material'
              checkedIcon='clear'
              uncheckedIcon='add'
              checkedColor='red'
              onPress={() => this.selectDocument(note.id)}
              checked={note.checked}
            />
            })
          }

        </ScrollView>
        </View>
    }
}