import React from 'react';
import { Text, View, Button, TextInput, ScrollView, Platform } from 'react-native';
import { ListItem, Icon, Card, Overlay, Button as StyledButton } from 'react-native-elements'
import {MenuService} from './DomainService';
import styles from './Styles'
  
export class SideMenuManageScreen extends React.Component{
    constructor(props)
    {
      super(props)
      this.state = {
        submenu : [],
        contentHistoryStack: [],
        currentContent: {},
        previousContent: {},
        menuContent : {},
        phrase : "",
        previousKey : "",
        currentTextInsert: "",
        shortcut: ""
      }


      new MenuService().getMenu().then((menu) => {
        this.setState({menuContent: menu})

        this.setState({"previousContent": [menu]})
        this.setState({"currentContent": menu})
      })

      this.handlePress = this.handlePress.bind(this);
      this.handleEdit = this.handleEdit.bind(this);
      this.handleDelete = this.handleDelete.bind(this);
      this.categoryPress = this.categoryPress.bind(this);

    }

    handleAddPress = () => {

    }

    saveInput = () => {
      if(this.state["phrase"].length === 0)
        return
      this.state["currentContent"][this.state["phrase"]] = {text: this.state["phrase"], "shortcut": this.state["shortcut"], "items" : {}}
      console.log(this.state)
      let path = ""
      if(this.state["contentHistoryStack"].length >= 1)
        path = this.state["contentHistoryStack"].join("%%")
      let obj = {}
      if(this.state["currentContent"])
        obj = this.set(path, this.state["currentContent"], this.state["menuContent"])
      
      this.setState({phrase: ""})
      new MenuService().saveMenu(this.state["menuContent"])
    }

    set(path, value, obj) {
      var schema = obj;  // a moving reference to internal objects within obj
      var pList = path.split('%%');
      var len = pList.length;
      for(var i = 0; i < len-1; i++) {
          var elem = pList[i];
          if( !schema[elem] ) schema[elem] = {}
          schema = schema[elem];
      }
      if(schema[pList[len-1]] != null)
        schema[pList[len-1]] = value;

      return schema
    }

    delete(path, obj)
    {
      var schema = obj;  // a moving reference to internal objects within obj
      var pList = path.split('%%');
      var len = pList.length;
      for(var i = 0; i < len-1; i++) {
          var elem = pList[i];
          schema = schema[elem];
      }
      if(schema)
        delete schema[pList[len-1]]

      return schema
    }

    get(path, obj)
    {
      var schema = obj;  // a moving reference to internal objects within obj
      var pList = path.split('%%');
      var len = pList.length;
      for(var i = 0; i < len-1; i++) {
          var elem = pList[i];
          if( !schema[elem] ) schema[elem] = {}
          schema = schema[elem];
      }
      return schema 
    }

    handleEdit = (key, text, shortcut) => {
      let path = ""
      if(this.state["contentHistoryStack"].length >= 1)
        path = this.state["contentHistoryStack"].join("%%")
      
      let obj = {}

      let newState = {}

      if(this.state["currentContent"]) {
        for (const [k, v] of Object.entries(this.state["currentContent"])) {
          if(key == k)
          {
            newState[text] = v
            newState[text].text = text
            newState[text].shortcut = shortcut
          }
          else
            newState[k] = this.state["currentContent"][k]
        }
        obj = this.set(path, newState, this.state["menuContent"])
        console.log(newState)
      }
        
      
      //this.setState({phrase: ""})
      new MenuService().saveMenu(this.state["menuContent"])
      this.setState({menuContent: this.state["menuContent"]})      
    }

    handleDelete = (key) => {    
      let path = ""
      if(this.state["contentHistoryStack"].length >= 1)
      {
        path = this.state["contentHistoryStack"].join("%%")
        path += "%%" + key
      }
      else
      {
        path += key
      }
        
      
      let obj = {}
      if(this.state["currentContent"])
        obj = this.delete(path, this.state["menuContent"])
      
      //this.setState({phrase: ""})
      new MenuService().saveMenu(this.state["menuContent"])
      this.setState({menuContent: this.state["menuContent"]})

    }

    handlePress = (key, currentContent, previousContent, enter) => {
      console.log("--AAAA----------")     
      let current = currentContent["items"]
      console.log(current)
      if(current == {})
      {
        //console.log(currentContent.text)
        if(enter === true)
        {
          this.setState({"currentContent": {}})
          this.setState({"previousContent": this.state.arrayvar.concat(newelement)})
        }
        else
        {
          this.props.navigation.state.routes[0].params.addTextContent(currentContent.text);
          this.props.navigation.closeDrawer();
        }

      }
      else
      {
        this.setState({"currentContent": current})
        this.setState({"previousContent": this.state["previousContent"].concat(previousContent)})
        //this.state["previousContent"].push(previousContent)
      }

      let arr = this.state["contentHistoryStack"]
      arr.push(key)
      arr.push("items")
      console.log("Content History Stack")
      console.log(arr)
      this.setState({"contentHistoryStack": arr})
    }
  
    categoryPress() {
      console.log("Category Press")
      console.log(this.state)
      if(this.state["previousContent"].length >1) 
        this.setState({"currentContent": this.state["previousContent"].pop()})


        let arr = this.state["contentHistoryStack"]
        arr.pop()
        arr.pop()
        this.setState({"contentHistoryStack": arr})
        
      
    }

    getTitle = () => {
      console.log("previous content")

      obj = this.getPreviousMenuObject()
      
      console.log(obj)
      return "" + obj.text
    }

    getPreviousMenuObject() {
      let path = ""
      if(this.state["contentHistoryStack"].length >= 1)
        path = this.state["contentHistoryStack"].join("%%")
      obj = {}
      if(this.state["currentContent"])
        obj = this.get(path, this.state["menuContent"])

      return obj
    }

    formatTitle(currentContent) {
      return currentContent.shortcut? currentContent.text + " [" + currentContent.shortcut + "]" : currentContent.text + " [No Shortcut Set]";
    }

    render(){
      return (
        <View style={styles.container}>
          <Button style={{height:40}} onPress={() => {this.categoryPress()}} title="Up Category"/>       

          <View style={styles.menuHeader}>
          </View>
          <ScrollView style={{flex:1}}>
            <View style={{flex:1, flexDirection: 'column'}}>
              {
                Object.keys(this.state.currentContent).map((key) => {
                  //<MenuItem key={key} val={val} handler={this.handleClick.bind} />
                  if(Platform.OS === 'web')
                    return <MenuItemWeb 
                      key={key} key1={key} 
                      menuTitle={this.formatTitle(this.state.currentContent[key])} 
                      phrase={this.state.currentContent[key].text} 
                      shortcut={this.state.currentContent[key].shortcut} 
                      menuVal={this.state.currentContent[key]} 
                      currentContent={this.state.currentContent} 
                      handler={this.handlePress} 
                      handlerEdit={this.handleEdit} 
                      handlerDelete={this.handleDelete} 
                      addHandler={this.handleAddPress} 
                    />
                  else
                    return <MenuItem key={key} key1={key} menuTitle={this.state.currentContent[key].text} menuVal={this.state.currentContent[key]} currentContent={this.state.currentContent} handler={this.handlePress} handlerEdit={this.handleEdit} handlerDelete={this.handleDelete} addHandler={this.handleAddPress} />
              })
              }
            </View>
            <View style={{marginTop: 40}}>
              <View style={{flex:1, flexDirection:'row'}}>
                <TextInput
                  onChangeText={(phrase) => this.setState({phrase: phrase})}
                  value={this.state.phrase}
                  placeholder="Enter a category or phrase"
                  style={{fontSize:16, paddingLeft: 10, paddingTop:5, paddingBottom:5, marginLeft: 10, marginRight: 10, borderBottomColor: 'grey', borderWidth: 1, flex: 2}}
                />

                <TextInput
                  onChangeText={(shortcut) => this.setState({shortcut: shortcut})}
                  value={this.state.shortcut}
                  placeholder="Enter a shortcut"
                  style={{fontSize:16, paddingLeft: 10, paddingTop:5, paddingBottom:5, marginLeft: 10, marginRight: 10, borderBottomColor: 'grey', borderWidth: 1, flex: 1}}
                />
                
                {
                  (Platform.OS === 'web')?
                  <StyledButton leftIcon={{name: 'save'}} buttonStyle={styles.webButton2} style={{flex:1}} onPress={() => {this.saveInput()}} title="Save"/>
                  :
                  <Button style={{height:40}} onPress={() => {this.saveInput()}} title="Save"/>

                }
                </View>
            </View>

          </ScrollView>
          <View style={styles.footerContainer}>
          </View>
        </View>
      );
    }
  }

  class MenuItemWeb extends React.Component{
    constructor(props){
      super(props)
      console.log(props)

      this.state = {
        phrase: "",
        shortcut: "",
        showInput: false,
        showEditInput: false,
        phrase: "",
        shortcut: ""
      }
    }

    showInput() {
      this.setState({showInput: true})
    }

    saveInput()
    {
      this.setState({showInput: false})
    }

    editAction() {
      this.setState({showEditInput: !this.state.showEditInput})
      this.setState({phrase: this.props.phrase, shortcut: this.props.shortcut})
    }

    saveEditAction() {
      this.editAction();
      this.props.handlerEdit(this.props.key1, this.state.phrase, this.state.shortcut)
    }
  
    render(){
      return (
          <View style={{flex:1, flexDirection: 'row', justifyContent: "left"}}>
            <View style={{flex:1, flexDirection: 'column'}}>
            { !this.state.showEditInput && <StyledButton buttonStyle={styles.webButton3} color="#000" type="clear" small onPress={() => {this.props.handler(this.props.key1, this.props.menuVal, this.props.currentContent, false)}} title={this.props.menuTitle}/> }
            { this.state.showEditInput && <View style={{flexDirection: 'row'}}>
              <TextInput
                  onChangeText={(phrase) => this.setState({phrase: phrase})}
                  value={this.state.phrase}
                  placeholder="Enter a category or phrase"
                  style={{fontSize:16, paddingLeft: 10, paddingTop:5, paddingBottom:5, marginLeft: 10, marginRight: 10, borderBottomColor: 'grey', borderWidth: 1, flex: 2}}
                />

                <TextInput
                  onChangeText={(shortcut) => this.setState({shortcut: shortcut})}
                  value={this.state.shortcut}
                  placeholder="Enter a shortcut"
                  style={{fontSize:16, paddingLeft: 10, paddingTop:5, paddingBottom:5, marginLeft: 10, marginRight: 10, borderBottomColor: 'grey', borderWidth: 1, flex: 1}}
                />
            </View> }
            </View>
            <View style={{marginBottom: 10, flexDirection:'row'}}>
            { this.state.showEditInput && <StyledButton leftIcon={{name: 'save'}} containerStyle={{marginRight: 5}}   type="outline" small onPress={() => this.saveEditAction()} title="Update item"/> }           
            { !this.state.showEditInput && <StyledButton leftIcon={{name: 'edit'}} containerStyle={{marginRight: 5}}  color="#fff" type="outline" small onPress={() => {this.editAction(this.props.key1)}} title="Edit"/> }
            { !this.state.showEditInput && <StyledButton leftIcon={{name: 'delete'}} containerStyle={{marginRight: 5}} buttonStyle= {{borderColor: 'red'}} titleStyle={{color: 'red'}}  color="#F00" type="outline"  small onPress={() => {this.props.handlerDelete(this.props.key1)}} title="Delete"/> }
            { !this.state.showEditInput && this.props.menuVal["items"] === undefined && <StyledButton buttonStyle={styles.webButton2} color="#000" small titleStyle={{color: '#000000'}} onPress={() => {this.props.handler(this.props.key1, this.props.menuVal, this.props.currentContent, true)}} title="Add item"/>}
            </View>
        </View>
      )
    }
  }
  
  class MenuItem extends React.Component{
    constructor(props){
      super(props)
      console.log(props)

      this.state = {
        phrase: "",
        showInput: false
      }
    }

    showInput() {
      this.setState({showInput: true})
    }

    saveInput()
    {
      this.setState({showInput: false})
    }
  
    render(){
      return (
        <View style={{flex:1}}>
          <View style={{flex:1, flexDirection: 'row', justifyContent: "left"}}>
            <Button style={{height:40}} onPress={() => {this.props.handler(this.props.key1, this.props.menuVal, this.props.currentContent, false)}} title={this.props.menuTitle}/>
            {//<Button style={{height:40}} onPress={() => {this.props.handlerEdit(this.props.key1, this.props.menuVal, this.props.currentContent, false)}} title="Edit"/>
            }
            <Button style={{height:40}} onPress={() => {this.props.handlerDelete(this.props.key1, this.props.menuVal, this.props.currentContent, false)}} title="Delete"/>

            {  this.props.menuVal["items"] === undefined &&
            <Button style={{height:40}} onPress={() => {this.props.handler(this.props.key1, this.props.menuVal, this.props.currentContent, true)}} title="Add item"/>
            }
          </View>
          { this.state.showInput &&
          <View style={{flex: 0, marginLeft: 10}}>
            <TextInput
                onChangeText={(phrase) => this.setState({phrase: phrase})}
                value={this.state.phrase}
                placeholder="Enter a category or phrase"
                style={{flex:1}}
              />
            <Button style={{height:40}} onPress={() => {this.saveInput()}} title="Save"/>
          </View>
        }
        </View>
      )
    }
  }