import React from 'react';
import {Text, View, Button, TextInput, Switch } from 'react-native';
import {Divider, ListItem, Icon, Overlay, ButtonGroup, Button as StyledButton, SearchBar, Input } from 'react-native-elements'
import {TokenComponent} from './TokenComponent'
import {SettingsService} from "./DomainService"
import { BackupService } from './BackupService'

export class SettingsScreen extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            settings: {},
            backupFileContent: ""
        }

        this.styles = {
            titleText: {
                fontSize: 20,
                fontWeight: "bold"
            }
        }

        new SettingsService().getSettings().then((settings) =>{
            if(settings.name === undefined) settings.name = "Steven Wesley"
            if(settings.richTextEnabled === undefined) settings.richTextEnabled = true
            console.log(settings)
            this.setState({settings: settings})
        })
        
    }

    clearSignature() {
        let settings = this.state.settings
        settings.signature = null
        this.setState({settings: settings})
        new SettingsService().setSettings(this.state.settings)        
    }
    nameChange(name) {
        let settings = this.state.settings
        settings.name = name
        this.setState({settings: settings})
        new SettingsService().setSettings(this.state.settings)
    }

    imageUpload = (e) => {
        const file = e.target.files[0];
        this.getBase64(file).then(base64 => {
            let settings = this.state.settings
            settings.signature = base64
            this.setState({settings: settings})
            new SettingsService().setSettings(this.state.settings)
            console.debug("file stored",base64);
        });
    };
  
    restoreBackupFile() {
        if(this.state.backupFileContent !== "")
            new BackupService().restoreFromBackup(this.state.backupFileContent)
    }

    getBase64(file) {
        return new Promise((resolve,reject) => {
           const reader = new FileReader();
           reader.onload = () => resolve(reader.result);
           reader.onerror = error => reject(error);
           reader.readAsDataURL(file);
        });
      }

    toggleRichTextSwitch() {
        let settings = this.state.settings
        settings.richTextEnabled = !settings.richTextEnabled
        this.setState({settings: settings})
        new SettingsService().setSettings(settings)
    }

    downloadContent() {
        new BackupService("file").doBackup();
    }

    clearImages() {
        new SettingsService().clearSignatures();
    }

    render() {
        return (
            <View>
                <View style={{marginBottom: 10}}>
                    <Text style={{fontSize: 40}}>Personal Settings</Text>
                    <Divider style={{ backgroundColor: 'grey' }} />
                </View>
                <View style={{marginBottom: 10}}>
                    <Text style={this.styles.titleText}>Your name as you want it to appear on documents</Text>
                    <TextInput 
                        placeholder="Enter your name"
                        style={{fontSize:16, borderWidth: 1, borderColor: '#eeeeee'}}
                        onChangeText={(text) => this.nameChange(text)}
                        value={this.state.settings.name}
                    ></TextInput>
                </View>
                <View style={{marginBottom: 10}}>
                    <Text style={this.styles.titleText}>Signature</Text>
                    <input 
                        accept="image/png, image/jpeg"
                        type="file" 
                        id="imageFile" 
                        name='imageFile' 
                        onChange={this.imageUpload} />
                    <View style={{flex:1,flexDirection: "row"}}>
                    { this.state.settings.signature && <Button title="clear" onPress={() => this.clearSignature()}></Button> }    
                    { this.state.settings.signature && <img src={this.state.settings.signature} width="400" height="100"/> }
                    </View>
                </View>
                <View style={{marginBottom: 10}}>
                    <Text style={{fontSize: 40}}>App Settings</Text>
                    <Divider style={{ backgroundColor: 'grey' }} />
                </View>
                <View style={{marginBottom: 10}}>
                    <Text style={this.styles.titleText}>Enable Rich text editor</Text>
                    <Switch
                        trackColor={{ false: "#767577", true: "#81b0ff" }}
                        thumbColor={this.state.settings.richTextEnabled ? "#f5dd4b" : "#f4f3f4"}
                        ios_backgroundColor="#3e3e3e"
                        onValueChange={()=> this.toggleRichTextSwitch()}
                        value={this.state.settings.richTextEnabled}
                    />
                </View>
                <View style={{marginBottom: 10}}>
                    <Text style={this.styles.titleText}>Token key (keep secret)</Text>
                    <TextInput 
                        editable={false}
                        style={{fontSize:16, borderWidth: 1, borderColor: '#eeeeee'}}
                        placeholder="key"
                        value={this.state.settings.key}
                    ></TextInput>
                </View>
                <View style={{marginBottom: 10}}>
                    <Text style={this.styles.titleText}>Restore from Backup</Text>
                    <TextInput 
                        style={{fontSize:16, borderWidth: 1, borderColor: '#eeeeee'}}
                        placeholder="Paste the contents of the backup file here"
                        value={this.state.backupFileContent}
                        onChangeText={(val) => this.setState({backupFileContent: val})}
                    ></TextInput>
                    <Button title="Restore content from (backup)" onPress={() => this.restoreBackupFile()}></Button>
                </View>
                <View style={{marginBottom: 10}}>
                    <Button title="Download all content (backup)" onPress={() => this.downloadContent()}></Button>
                </View>
                <View style={{marginBottom: 10}}>
                    <Button title="Remove all signatures from docs" onPress={() => this.clearImages()}></Button>
                </View>


            </View>
        )
    }
}