import {SettingsService} from "./DomainService"

export class TokenComponent {
    createOrGetToken(){
        let s = new SettingsService();

        return s.getSettings().then((settings) =>{
            if(settings && settings["key"]) {
                return settings["key"];
            }
            else {
                settings["key"] = new Array(100).fill().map(() => String.fromCharCode(Math.random()*86+40)).join("");
                s.setSettings(settings);
            }
        });
    }
}