import React from 'react';
import {Platform, StyleSheet,ImageBackground, Text, View, Button, TextInput, Dimensions,KeyboardAvoidingView, StatusBar, ScrollView, FlatList, Keyboard, Alert } from 'react-native';
import { ListItem, Icon, Overlay, ButtonGroup, Button as StyledButton, SearchBar } from 'react-native-elements'
import 'react-quill/dist/quill.snow.css';
import jsPDF from 'jspdf';
import {MedicalOrderSheet, TextTemplate, MedicalOrderSheetImage, MedicalOrderSheetImageCombined} from './Templates';
import html2pdf from 'html2pdf.js';
import {TextUtility} from './Utilities'
  
  
export class ToolbarComponent extends React.Component {
    constructor(props){
      super(props)
      this.state= {
        history: [this.props.text],
        position: 0,
        noteId: this.props.noteId,
        generatedDate: null,
      }
      this.jspdfOpt = {
        jsPDF: { unit: 'in', format: 'letter', orientation: 'portrait' },
      }

      this.jspdfOpt2 = {
        jsPDF: { unit: 'in', format: 'letter', orientation: 'portrait' },
        margin: [0.5, 0.3, 0.5, 0.3]
      }

      this.jspdfOpt3 = {
        jsPDF: { unit: 'in', format: 'letter', orientation: 'portrait' },
        margin: [0.5, 0.0, 0.5, 0.0],
        pagebreak: { mode: 'avoid-all'}
      }
      this.ready = true
    }

    readyToType() {
      if (this.ready) {
        this.ready = false
        setTimeout(() => { this.ready = true }, 1000)
        return true
      }
      return false
    }

    extractSignature(text) {
      return this.props.text.replace(/(<img src="(.*)">)/gi, `<img id="signature" src="" />`)
    }

    componentDidUpdate(prevProps) {

      if (prevProps.text !== this.props.text) {
        if(this.props.noteId != this.state.noteId){
          this.setState({noteId: this.props.noteId, history: [this.props.text], position: 0})
          return
        }
        if(this.props.undo === true) return; 
        if(this.state.history && this.props.text === this.state.history[this.state.position]) return
        
        if(!this.readyToType()) return 
        //console.log("Saving " + this.props.text)
        let history = this.state.history
        
        if(history.length > 15)
          history[this.state.position-15] = null

        if(history[this.state.position+1])
        {
          history[this.state.position+1] = this.props.text
          this.setState({history: history, position: this.state.position+1})
        }
        else this.setState({history: this.state.history.concat([this.props.text]), position: this.state.position+1})
      }
    }

    undo() {
      let history = this.state.history
      if(this.state.history[this.state.position-1] === null) return
      if(this.state.position > 0) history.pop()
      if(this.state.position > 0) this.setState({position: this.state.position - 1, history: history}) 
      if(this.state.position) this.props.textHandler(this.state.history[this.state.position-1])
    }

    combineNotes() {
      this.props.saveNote()
      this.props.combineAction()
    }

    getUpdatedDate() {
      let d = this.props.generatedDate? this.props.generatedDate : new Date();
      this.props.saveDate(d)
      this.setState({generatedDate: d})
      return d
    }

    combinePdf() {  
        if(!this.props.richText) return
        this.setState({isVisible: false})
        let d = this.getUpdatedDate()
        if(this.props.createdDate)
          d = new Date(this.props.createdDate)
        let order = ""
        order = this.getOrderText().split(/(?:\r\n|\r|\n)/g).filter((item) => item != "")
        let physicianName = this.props.appSettings.name? this.props.appSettings.name:'Steven Wesley';
        let templateTime = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"][d.getMonth()] +"-" + d.getDate() + "-" + d.getFullYear();

        (new html2pdf.Worker).from(
            new TextTemplate().generateFromDict({
                note: this.props.text.replace(/[\u0008\u0014]/g, "").replace(/{{signature}}/gm, this.props.appSettings.signature)
            })
        ).set(this.jspdfOpt3).toPdf().get('pdf')
        .then( (pdf) => {
          this.pdfHeader(pdf)
        })
        .from(
            new MedicalOrderSheetImageCombined().generateFromDict({
                datetime: templateTime, 
                lastname: this.props.patient.split(/[, ]+/)[0], 
                firstname: this.props.patient.split(/[, ]+/)[1], 
                order0: order.slice(0,5).join("<br/>"),
                order1: order.slice(5,10).join("<br/>"), 
                physicianName: physicianName,
                physicianName1: order.slice(5,10).length > 0? physicianName: "",
                signature: this.props.appSettings.signature,
                signature1: order.slice(5,10).length > 0? this.props.appSettings.signature: "",
                datetime1: order.slice(5,10).length > 0? templateTime: "",
                backgroundimage: require("./Assets/MedicalOrderSheet-2.jpg"),
                logo: require("./Assets/ontario.jpg")
            })
        ).toContainer().set(this.jspdfOpt3).toCanvas().toPdf().save();

        this.setState({isVisible: false})
    }

    pdfHeader(pdf) {
      console.log(pdf.internal)
      var totalPages = pdf.internal.getNumberOfPages();

      for (var i = 1; i <= totalPages; i++) {
          pdf.setPage(i);
          pdf.setFontSize(11);
          pdf.setTextColor(0);
          
          pdf.text("Name: " + this.props.patient, 0.2, 0.3);
          pdf.text('DOB ________________ OTIS #: _______________________', 2.8, 0.3);
          //pdf.text("Date: " + this.state.generatedDate.toLocaleDateString(), 7.2, 0.3);
          if(this.props.createdDate)
            pdf.text("Date: " + new Date(this.props.createdDate).toLocaleDateString(), 7.2, 0.3);
          pdf.text(' Page ' + i + ' of ' + totalPages, 4, pdf.internal.pageSize.getHeight()-0.3);
      }         

      pdf.addPage();
    }

    saveAsWord() {
      if(this.props.richText){
        var doc = document.createElement("div");
        doc.innerHTML = new TextTemplate().generateFromDict({
          note: this.props.text.replace(/{{signature}}/gm, "")
        })
        this.export2Doc(doc, "file")
      }
    }

    export2Doc(element, filename = ''){
      var preHtml = "<html xmlns:o='urn:schemas-microsoft-com:office:office' xmlns:w='urn:schemas-microsoft-com:office:word' xmlns='http://www.w3.org/TR/REC-html40'><head><meta charset='utf-8'><title>Export HTML To Doc</title></head><body>";
      var postHtml = "</body></html>";
      var html = preHtml+element.innerHTML+postHtml;
  
      var blob = new Blob(['\ufeff', html], {
          type: 'application/msword'
      });
      
      // Specify link url
      var url = 'data:application/vnd.ms-word;charset=utf-8,' + encodeURIComponent(html);
      
      // Specify file name
      filename = filename?filename+'.doc':'document.doc';
      
      // Create download link element
      var downloadLink = document.createElement("a");
  
      document.body.appendChild(downloadLink);
      
      if(navigator.msSaveOrOpenBlob ){
          navigator.msSaveOrOpenBlob(blob, filename);
      }else{
          // Create a link to the file
          downloadLink.href = url;
          
          // Setting the file name
          downloadLink.download = filename;
          
          //triggering the function
          downloadLink.click();
      }
      
      document.body.removeChild(downloadLink);
  }

    saveAsPdf() {
        this.props.saveNote()
        if(this.props.richText){
            //let order = this.getOrderText().split(/(?:\r\n|\r|\n)/g).filter((item) => item != "")

            (new html2pdf.Worker).from(new TextTemplate().generateFromDict({
                note: this.props.text.replace(/{{signature}}/gm, this.props.appSettings.signature)
            })).set(this.jspdfOpt2).toPdf().get('pdf').then((pdf) => {
              this.pdfHeader(pdf)
            }).save();
        }
        else {
            var doc = new jsPDF('p', 'pt');
        
            doc.setFont('Helvetica Neue')
            
            doc.setFontType("bold");
            doc.setFontSize(12);
            doc.text(20, 40, 'Patient Note')
    
            doc.setFontType('normal')
            var splitText = doc.splitTextToSize(this.state.history[this.state.position], 500);
            for (var i=0; i<splitText.length; i++){
            doc.text(20, 60, splitText)
            }
            
            
            // Save the Data
            doc.save('Note.pdf')
        }


        this.setState({isVisible: false})
    }

    saveOrderSheetPdf() {
      let orderSheet = new MedicalOrderSheetImage()
      this.setState({isVisible: false})
      let d = this.getUpdatedDate()
      let order = ""
      order = this.getOrderText().split(/(?:\r\n|\r|\n)/g).filter((item) => item != "")
      let physicianName = this.props.appSettings.name? this.props.appSettings.name:'Steven Wesley';
      let templateTime = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"][d.getMonth()] +"-" + d.getDate() + "-" + d.getFullYear();

      
      html2pdf(orderSheet.generateFromDict({
        datetime: templateTime, 
        lastname: this.props.patient.split(/[, ]+/)[0], 
        firstname: this.props.patient.split(/[, ]+/)[1], 
        order0: order.slice(0,5).join("<br/>"),
        order1: order.slice(5,10).join("<br/>"), 
        physicianName: physicianName,
        physicianName1: order.slice(5,10).length > 0? physicianName: "",
        signature: this.props.appSettings.signature,
        signature1: order.slice(5,10).length > 0? this.props.appSettings.signature: "",
        datetime1: order.slice(5,10).length > 0? templateTime: "",
        backgroundimage: require("./Assets/MedicalOrderSheet-1.jpg"),
        logo: require("./Assets/ontario.jpg")}), this.jspdfOpt)
    }

    getOrderText() {
        let text = ""
        if(this.props.richText) {
            text = this.props.text.replace(/<br\s*[\/]?>/gi, "\n")
            text = text.replace(/<p[^>]*>/g, '').replace(/<\/p>/g, '\n');
            text = new TextUtility().stripHtmlTags(text) 
            text = text.replace(/[\u0008\u0014]/g, "");
        }
        else {
            text = this.props.text
        }

        const regex = /^[P|p]lan[:]?([\s\S]*?)\n\n/smg;
        let m = regex.exec(text);
        if(m && m[1]) {
            return m[1]
        }
        else
            return ""
    }

    insertSignature() {
      this.props.insertHandler('<img width=160 height=40 alt="signature" src="{{signature}}" />')
    }

    showPdfToolbar()
    {
      this.setState({isVisible: true})
    }
    
    render(){
      return (
        <View style={{flex: 1, flexDirection: "row"}}>
        { this.state.isVisible === true &&
        <View>
          <StyledButton title="Save Content as PDF" onPress={() => this.saveAsPdf()}></StyledButton>
          <StyledButton title="Create Medical Order Sheet PDF" onPress={() => this.saveOrderSheetPdf()}></StyledButton>
          <StyledButton title="Combine Notes and Medical Order Sheet PDF" onPress={() => this.combinePdf()}></StyledButton>
          <StyledButton title="Combined Multiple Notes PDF" onPress={() => this.combineNotes()}></StyledButton>
        </View>
        }
        <Icon
          name='undo'
          type='font-awesome'
          color='#aaa'
          onPress={() => this.undo()}
          />
        <Icon
          name='picture-as-pdf'
          color='#aaa'
          onPress={() => this.showPdfToolbar()}
          />
        <Icon
          name='note'
          color='#aaa'
          onPress={() => this.saveAsWord()}
          />
        <Icon
            name='gesture'
            color='#aaa'
            onPress={() => this.insertSignature()}
          />
        </View>
        
      )
    }
  }

