  import React from 'react';
import {Platform, StyleSheet,ImageBackground, Text, View, Button, TextInput, Dimensions,KeyboardAvoidingView, StatusBar, ScrollView, FlatList, Keyboard, Alert } from 'react-native';
import { ListItem, Icon, Overlay, ButtonGroup, Button as StyledButton, SearchBar } from 'react-native-elements'
import styles from './Styles'
import {PatientService, MenuService} from './DomainService'
import 'react-quill/dist/quill.snow.css';
import {TextUtility} from './Utilities'
  
  
export class TabComponent extends React.Component {
    constructor(props){
      super(props)
      this.state = {
        data: [],
        selectedIndex: -1,
        buttons: []
      }
      this.refreshTabs()
    }

    refreshTabs(){
      let patientService = new PatientService()
      patientService.getLastOpenedNotes().then((notes) => {
        this.setState({data: notes})
        if(this.state.selectedIndex === -1) this.setState({selectedIndex: notes.findIndex((note) => { return note.id === this.props.currentNoteId})})
        this.setState({buttons: notes.filter(item => { return item.text !== undefined}) })
      })
    }

    tabPress(item)
    {
      this.props.noteHandler(item)
      this.refreshTabs()
    }

    updateIndex (selectedIndex) {
      this.tabPress(this.state.buttons[selectedIndex])
      this.setState({selectedIndex: selectedIndex})
    }

    shouldComponentUpdate(nextProps, nextState) {
      if (this.props.currentNoteId === nextProps.currentNoteId && this.state.buttons.length) {
        return false;
      } else {
        return true;
      }
    }

    formatTab(text) {
      let plainText = new TextUtility().stripHtmlTags(text)
      return plainText.replace(/\n/g, ' ').slice(0, 10) + "..."  
    }

    render() {
      return (
        <View style={{flex: 1, flexDirection: 'row', borderRadius: 10, backgroundColor: "#eee"}}>
          { Platform.OS === 'web'?
            <ButtonGroup
                style={{width:'100%'}}
                onPress={(index) => this.updateIndex(index)}
                selectedIndex={this.state.selectedIndex}
                containerStyle={{flex:1}}
                textStyle={{fontWeight: 'normal', fontSize: 14}}
                buttons={this.state.buttons.map((item) => { return this.formatTab(item.text)})}
              />
            :
            this.state.data.filter(item => {
              return item.text !== undefined
            }).map((item) => {
                let txt = item.text.slice(0, 7) + ".."
              return <Button title={txt.replace(/\n/g, ' ') + "..."} key={item.id} onPress={() => {this.tabPress(item)}}/>    
            })
          }
        </View>
      )
    }
  }
