import React from 'react';
import {Platform, StyleSheet, Text, View, Button, TextInput, Dimensions,KeyboardAvoidingView, StatusBar, ScrollView, FlatList } from 'react-native';
import {Icon, Card, Overlay, ButtonGroup, Button as StyledButton } from 'react-native-elements'

import { Constants } from 'expo';
import {MenuService} from './DomainService';
import styles from './Styles'


export class SideMenu extends React.Component{
    constructor(props)
    {
      super(props)
      this.state = {
        submenu : [],
        contentHistoryStack: [],
        currentContent: {},
        previousContent: {},
        menuContent : {},
        phrase : "",
        previousKey : "",
        currentTextInsert: ""
      }
      this.resetMenu();
    }

    resetMenu(){
      new MenuService().getMenu().then((menu) => {
        this.setState({menuContent: menu})

        this.state["previousContent"] = []
        this.state["previousContent"].push(this.state["menuContent"])
        this.state["currentContent"] = this.state["menuContent"]
        this.setState({"contentHistoryStack": []})
      })
    }

    handleAddPress = () => {

    }

    saveInput = () => {
      if(this.state["phrase"].length === 0)
        return
      this.state["currentContent"][this.state["phrase"]] = {text: this.state["phrase"], "items" : {}}
      console.log(this.state)
      let path = ""
      if(this.state["contentHistoryStack"].length >= 1)
        path = this.state["contentHistoryStack"].join("%%")
      let obj = {}
      if(this.state["currentContent"])
        obj = this.set(path, this.state["currentContent"], this.state["menuContent"])
      
      this.setState({phrase: ""})
      new MenuService().saveMenu(this.state["menuContent"])
    }

    set(path, value, obj) {
      var schema = obj;  // a moving reference to internal objects within obj
      var pList = path.split('%%');
      var len = pList.length;
      for(var i = 0; i < len-1; i++) {
          var elem = pList[i];
          if( !schema[elem] ) schema[elem] = {}
          schema = schema[elem];
      }
      if(schema[pList[len-1]] != null)
        schema[pList[len-1]] = value;

      return schema
    }

    get(path, obj)
    {
      var schema = obj;  // a moving reference to internal objects within obj
      var pList = path.split('%%');
      var len = pList.length;
      for(var i = 0; i < len-1; i++) {
          var elem = pList[i];
          if( !schema[elem] ) schema[elem] = {}
          schema = schema[elem];
      }
      return schema 
    }

    handlePress = (key, currentContent, previousContent, enter) => {
      console.log("------------")     
      let current = currentContent["items"]
      console.log(current)
      if(current == {})
      {
        //console.log(currentContent.text)
        if(enter === true)
        {
          this.setState({"currentContent": {}})
          this.state["previousContent"].push(previousContent)
        }
        else
        {
          this.props.navigation.state.routes[0].params.addTextContent(currentContent.text);
          this.props.navigation.closeDrawer();
        }

      }
      else
      {
        this.setState({"currentContent": current})
        this.state["previousContent"].push(previousContent)
      }

      let arr = this.state["contentHistoryStack"]
      arr.push(key)
      arr.push("items")
      console.log(arr)
      this.setState({"contentHistoryStack": arr})
    }
  
    categoryPress() {
      if(this.state["previousContent"].length >1) 
        this.setState({"currentContent": this.state["previousContent"].pop()})


        let arr = this.state["contentHistoryStack"]
        arr.pop()
        arr.pop()
        this.setState({"contentHistoryStack": arr})
             
    }

    topLevelPress() {
      this.resetMenu()
    }

    insertPressCurrent(currentContent) {
      this.props.navigation.state.routes[0].params.addTextContent(currentContent.text);
      this.props.navigation.closeDrawer();
    }

    insertPress(){
      let obj = this.getPreviousMenuObject()
      this.props.navigation.state.routes[0].params.addTextContent(this.capitalizeFirstLetter(obj.text));
      this.props.navigation.closeDrawer();
    }

    capitalizeFirstLetter(string) {
      return string.charAt(0).toUpperCase() + string.slice(1);
    }

    getTitle = () => {
      console.log("previous content")

      let obj = this.getPreviousMenuObject()
      
      console.log(obj)
      return "Insert \"" + obj.text + '"'
    }

    getPreviousMenuObject() {
      let path = ""
      if(this.state["contentHistoryStack"].length >= 1)
        path = this.state["contentHistoryStack"].join("%%")
      let obj = {}
      if(this.state["currentContent"])
        obj = this.get(path, this.state["menuContent"])

      return obj
    }

    render(){
      return (
        <View style={styles.container}>
          <Button style={{height:40}} onPress={() => {this.topLevelPress()}} title="Top Level"/> 
          <Button style={{height:40}} onPress={() => {this.categoryPress()}} title="Up Category"/>       

          <View style={styles.menuHeader}>
          </View>
          <ScrollView style={{flex:1}}>
            <View>
              {
                 Object.keys(this.state["currentContent"]).length == 0 && <Button style={{height:40}} onPress={() => {this.insertPress()}} title={this.getTitle()}/>
              }
              {
                
                Object.keys(this.state.currentContent).map((key) => (
                  //<MenuItem key={key} val={val} handler={this.handleClick.bind} />
                  <View style={{flex: 1, flexDirection: 'row', marginBottom:3}}>
                    <MenuItem style={{flex:1}} key1={key} insertPress={(title) => this.insertPressCurrent(title)} menuTitle={this.state.currentContent[key].text} menuVal={this.state.currentContent[key]} currentContent={this.state.currentContent} handler={this.handlePress} addHandler={this.handleAddPress} />
                  </View>
              ))
              }
            </View>

              <View style={{flex:1, flexDirection: 'row', justifyContent: "center", marginTop: 40}}>
                <TextInput
                    onChangeText={(phrase) => this.setState({phrase: phrase})}
                    value={this.state.phrase}
                    placeholder="Enter a category or phrase"
                    style={{flex:1, borderColor: 'black', borderBottomWidth: 1, marginLeft: 2, marginRight: 2}}
                  />
                <Button style={{height:40}} onPress={() => {this.saveInput()}} title="Save"/>
              </View>

          </ScrollView>
          <View style={styles.footerContainer}>
          </View>
        </View>
      );
    }
  }
  
  
  class MenuItem extends React.Component{
    constructor(props){
      super(props)
      console.log(props)

      this.state = {
        phrase: "",
        showInput: false
      }
    }

    showInput() {
      this.setState({showInput: true})
    }

    saveInput()
    {
      this.setState({showInput: false})
    }
  
    render(){
      return (
        <View style={{flex:1,  backgroundColor: '#eee',}}>
          <View style={{flex:1, flexDirection: 'row',}}>
            <View style={{height: 35,flex:1, flexDirection:'row'}}>
            {Platform.OS !== 'web'? 
              <Button style={{flex:1, flexDirection:'row'}} onPress={() => {this.props.handler(this.props.key1, this.props.menuVal, this.props.currentContent, false)}} title={this.props.menuTitle.substr(0, 25)}/>
              :
              <StyledButton buttonStyle={{backgroundColor: "#999"}} style={{flex:1, flexDirection:'row'}} onPress={() => {this.props.handler(this.props.key1, this.props.menuVal, this.props.currentContent, false)}} title={this.props.menuTitle.substr(0, 30)}/>
              }
            </View>
            <View>
            <Icon

              name='clone'
              type='font-awesome'
              color='#aaa'
              onPress={() => {this.props.insertPress(this.props.menuVal)}}
            />

            </View>
            {  this.props.menuVal["items"] === undefined &&
            <Button style={{height:40}} onPress={() => {this.props.handler(this.props.key1, this.props.menuVal, this.props.currentContent, true)}} title="Add item"/>
            }
          </View>
          { this.state.showInput &&
          <View style={{flex:1, flexDirection: 'row'}}>
            <TextInput
                onChangeText={(phrase) => this.setState({phrase: phrase})}
                value={this.state.phrase}
                placeholder="Enter a category or phrase"
              />
            <Button style={{height:40}} onPress={() => {this.saveInput()}} title="Save"/>
          </View>
        }
        </View>
      )
    }
  }

  export class SideMenuEmbedded extends SideMenu{
    insertPress(){
      let obj = this.getPreviousMenuObject()
      this.props.navigation.state.params.addTextContent(this.capitalizeFirstLetter(obj.text));
    }
    insertPressCurrent(currentContent) {
      this.props.navigation.state.params.addTextContent(this.capitalizeFirstLetter(currentContent.text));
    }
  }